import React, { Component } from 'react'
import Header from './components/header'
import Formulario from './components/formulario';
import Listagastos from './components/listagastos';
export default class App extends Component {

  constructor(props){
    super(props);
    // declaramos el starte
    this.state={
      gastos:[],
      presupuesto:1000,
      resto:1000
    }
  }
  
  agregarGasto=(objGasto)=>{
    let {gastos,resto}=this.state;
    gastos.push(objGasto);
    
    resto=resto-objGasto.monto;
    this.setState({
      gastos:gastos,
      resto:resto
    });    
  }
  render() {
    return (

      <React.Fragment>
        <Header>

        </Header>
        <div className="container mt-5">
          <div className="row">
            <div className="col-md-6">
              <Formulario agregarGasto={this.agregarGasto}></Formulario>
            </div>
            <div className="col-md-6">
              <Listagastos gastos={this.state.gastos} presupuesto={this.state.presupuesto} resto={this.state.resto}></Listagastos>
            </div>
          </div>
        </div>
      </React.Fragment>

    )
  }
}
